<script>
import CatalogTree from './CatalogTree.vue'

export default {
  data() {
    return {}
  },
  props: {
    catalogObj: {
      type: Object,
      default: ''
      // required: true,
      // validator: () => {}
    }
  },
  computed: {},
  created() {},
  methods: {},
  components: {
    CatalogTree
  }
}
</script>

<style lang="scss" scoped>
a {
  text-decoration: none;
  cursor: pointer;
}
a:hover {
  text-decoration: none;
}
a:focus {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
a:active {
  text-decoration: none;
}

.catalog-nav-div {
  // overflow: auto;
  // position: relative;
  // height: 500px;
}
</style>




